// 1. Hacker News ÌîºÎìú Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞
const ajax = new XMLHttpRequest();
const NEWS_URL = "https://api.hnpwa.com/v0/news/1.json";
ajax.open("GET", NEWS_URL, false);
ajax.send();
const newsFeed = JSON.parse(ajax.response);

let data = {};

// 2. newsFeed Î∂àÎü¨Ïò§Í∏∞
function getNewsFeed() {
  const source = `
    <ul>
      {{#each list}}
      <li>
        <div><a href="{{url}}">{{title}} ({{domain}})</a></div>
        <div>
          <span>{{points}} points by {{user}} {{time_ago}}</span>
          <span><a href="{{individual_url}}">{{comments_count}} comments</a></span>
        </div>
      </li>
      {{/each}}
    </ul>
    `;

  data = {
    list: newsFeed,
  };

  for (let i = 0; i < data.list.length; i++) {
    data.list[i].individual_url = `#${newsFeed[i].id}`;
  }

  let template = Handlebars.compile(source);
  document.querySelector(".container").innerHTML = template(data);
}

// 3. ÌÅ¥Î¶≠Ìïú Í∏ÄÏùò idÎ•º Ï†ÑÎã¨Ìï¥ÏÑú ÏΩòÌÖêÏ∏† ÌôîÎ©¥ Î∂àÎü¨Ïò§Í∏∞
function getIndividualContents(id) {
  const CONTENT_URL = `https://api.hnpwa.com/v0/item/${id}/json`;
  const source = `
  <div class="title">
    <h1>
      <a href="{{url}}">{{title}} ({{domain}})</a>
    </h1>
    <div>
      <span>{{points}} points by {{user}} {{time_ago}}</span>
      <span><a href="{{individual_url}}">{{comments_count}} comments</a></span>
    </div>
  </div>
  <ul>
    {{#each list}}
    <li>
      <div>{{user}} {{time_ago}}</div>
      <div>{{content}}</div>
    </li>
    {{/each}}
  </ul>
  `;

  // error: Ïôú data = { content }; Îäî ÎêòÏßÄ ÏïäÏùÑÍπå?
  ajax.open("GET", CONTENT_URL, false);
  ajax.send();
  const content = JSON.parse(ajax.response);
  const comments = JSON.parse(ajax.response).comments;
  data = {
    title: content.title,
    url: content.url,
    domain: content.domain,
    points: content.points,
    user: content.user,
    time_ago: content.time_ago,
    individual_url: `#${id}`,
    id: content.id,
    list: comments,
  };

  let template = Handlebars.compile(source);
  document.querySelector(".container").innerHTML = template(data);
}

// 4. ÎùºÏö∞ÌÑ∞ Íµ¨ÌòÑÌïòÍ∏∞
// Ïñ¥Îñ§ Ï†ïÎ≥¥Î•º Í∏∞Ï§ÄÏúºÎ°ú Îâ¥Ïä§ÌîºÎìúÏù∏ÏßÄ, Í≤åÏãúÎ¨º Ï†ïÎ≥¥Ïù∏ÏßÄ ÌôïÏù∏Ìï† Ïàò ÏûàÎÇò? => url(#) Îí§Ïóê Ïò§Îäî idÏùò Ïú†Î¨¥!
function router() {
  // ‚úÖerror Ìï¥Í≤∞ : Hash Î∞©ÏãùÏúºÎ°ú ÎùºÏö∞ÌÑ∞ Íµ¨ÌòÑ
  const hash = location.hash.substr(1, location.hash.length);
  if (hash === "") {
    getNewsFeed();
  } else {
    getIndividualContents(hash);
  }
}

window.addEventListener("hashchange", router);
// error2. Îâ¥Ïä§ÌîºÎìú => ÏΩòÌÖêÏ∏† ÌôîÎ©¥ => title ÎßÅÌÅ¨ => Îí§Î°úÍ∞ÄÍ∏∞
// ÌïòÎ©¥? Ïôú Îâ¥Ïä§ÌîºÎìúÎ°ú ÎêòÎèåÏïÑÏò¨Íπå? ÏΩòÌÖêÏ∏† ÌôîÎ©¥Ïù¥ ÎÇòÏôÄÏïº ÎêòÎäîÎç∞...
// ‚úÖÎ∞îÎ≥¥Í∞ôÏù¥ ÏïÑÎû´Ï§ÑÏóê router() ÎåÄÏã† getNewsFeed()Î•º Ïã§ÌñâÌñàÎã§.
// Ïô∏Î∂Ä ÎßÅÌÅ¨ÏóêÏÑú Îã§Ïãú Ïò¨ÎïåÎäî Î∏åÎùºÏö∞Ï†ÄÍ∞Ä ÏÉàÎ°ú Î°úÎî©ÎêòÎäî Í≤ÉÏù¥Îã§. Ï¶â, router()Ïù¥ Ïã§ÌñâÎêúÎã§.
// Ìï≠ÏÉÅ Ï≤´ÌôîÎ©¥(news Feed)Ïù¥ Ï∂úÎ†•ÎêúÎã§Îäî Î≥¥Ïû•ÏùÄ ÏóÜÎã§!
router();
